




















import Vue from "vue";
import loadMessages from "@/data/loadMessages.json";
export default Vue.extend({
	name: "DrpgLoader",
	props: {
		showLoader: Boolean,
	},
	methods: {
		randomLoadMessage() {
			const m = loadMessages as string[];

			const rnd = m[Math.floor(Math.random() * m.length)];
			return `${rnd}...` ?? "Loading";
		},
	},
});
